export default{
	data(){
		return{
			previewHidden: true,
			preurl:'',
			userid:''
		}
	},
	onLoad:function(options){
	 let promise1 = new Promise(function (resolve, reject) {
	   uni.getImageInfo({
			src: '../../static/erweimatu.jpg',
			success: function (res) {
			  console.log(res)
			  resolve(res);
			}
		  })
	  });
	 var that=this;
	let promise2 = new Promise(function (resolve, reject) {
		// 获取图片信息
	  uni.getImageInfo({
		src:'../../static/wexopid.jpg',
		success: function (res) {
		  console.log(res)
		  resolve(res);
		}
	  })
		});
	 Promise.all([
	      promise1, promise2
	    ]).then(res => {
	      console.log(res)
	      const ctx = uni.createCanvasContext('shareImg');
	      ctx.drawImage('../../' + res[0].path, 0, 0, 375, 606)
	      ctx.drawImage('../../' + res[1].path, 135, 488, 100, 100)
	      ctx.stroke()
	      ctx.draw()
	       });
		  var userid2=uni.getStorageSync('userId');
		  this.userid=userid2;
		  console.log("useridinva:",this.userid);
	},
	methods:{
	  share: function () {
			var that = this
			uni.showLoading({
			  title: '努力生成中...'
			})
		  uni.canvasToTempFilePath({
		    x: 0,
		    y: 0,
		    width: 375,
		    height: 606,
		    destWidth: 375,
		    destHeight: 606,
		    canvasId: 'shareImg',
		    success: function (res) {
		      console.log(res.tempFilePath);
				// 得到图片的网络地址
		       that.preurl=res.tempFilePath;
		      that.previewHidden=false;
			  uni.setStorageSync('filePath',that.preurl);
		      uni.hideLoading()
		    },
		    fail: function (res) {
		      console.log(res)
		    }
		  })
		 
		},
		 /**
		 * 保存到相册
		*/
		save: function () {
		  var that = this
		  uni.getSetting({
		    success(res) {
		      if (!res.authSetting['scope.writePhotosAlbum']) {
		        uni.authorize({
		          scope: 'scope.writePhotosAlbum',
		          success() {
		            that.startSaveImage()
		          }
		        })
		      }else{
		        that.startSaveImage()
		      }
		    }
		  })
		},
		startSaveImage: function () {
		  let that = this; 
			// 保存图片到系统相册
		  uni.saveImageToPhotosAlbum({
		    filePath: that.preurl,
		    success(res) {
		      uni.showModal({
		        content: '图片已保存到相册，赶紧晒一下吧~',
		        showCancel: false,
		        confirmText: '好哒',
		        confirmColor: '#72B9C3',
		        success: function (res) {
		          if (res.confirm) {
		            console.log('用户点击确定');
		            that.previewHidden=true;
		          }
		        }
		      })
		    }
		  })
		},
	},
	onShareAppMessage() {
		return{
			title:'邀请链接',
			path:'pages/index/index?oparentid='+this.UserId+'&channel=1',
			imageUrl:'../../static/00000.jpg'
		}
	}
}
